<template>
  <div class="layout">
    <Layout>
      <Header>
        <Menu mode="horizontal" theme="dark" active-name="1">
          <div class="layout-logo">welcome to my blog</div>
          <div class="layout-nav">
            <MenuItem name="1" class="iconText" @click.native="jump('codes')">
              <Icon type="ios-navigate"></Icon>
              codes
            </MenuItem>
            <MenuItem name="2" class="iconText" @click.native="jump('picture')">
              <Icon type="ios-keypad"></Icon>
              picture
            </MenuItem>
            <MenuItem name="3" class="iconText" @click.native="jump('music')">
              <Icon type="ios-analytics"></Icon>
              music
            </MenuItem>
            <MenuItem name="4" class="iconText" @click.native="jump('text')">
              <Icon type="ios-paper"></Icon>
              text
            </MenuItem>
          </div>
        </Menu>
      </Header>

      <Content :style="{ padding: '0 50px' }">
        <Breadcrumb :style="{ margin: '20px 0' }">
          <BreadcrumbItem class="bread"
            >This blog is created by treeyerr</BreadcrumbItem
          >
        </Breadcrumb>
        <div @mouseenter="handleBtnStatus(true)" @mouseleave="handleBtnStatus(false)">
          <swiper class="swiper" ref="mySwiper" :options="swiperOptions" >
          <swiper-slide class="swiper-item">
            <Card>
              <div class="content">
                <div class="time">{{ nowDate }} <br />{{ nowTime }}</div>
              </div>
            </Card>
          </swiper-slide>
          <swiper-slide>
            <Card>
              <div class="contentBox">
                <div class="info">
                  <div class="headImg"></div>
                  <div
                    class="signature"
                    :class="[currentIndex == 1 ? 'animate__animated animate__fadeInTopLeft' : '']"
                  >
                    Be yourself !
                  </div>
                </div>
              </div>
            </Card>
          </swiper-slide>
          <div class="swiper-button-prev" v-show="isShowBtn" slot="button-prev"></div>
          <div class="swiper-button-next" v-show="isShowBtn" slot="button-next"></div>
        </swiper>
        </div>
      </Content>
      <Footer class="layout-footer-center">2022/7/12 &copy; Treeyerr</Footer>
    </Layout>
  </div>
</template>

<script>
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  name: "Home",
  data() {
    const _self = this;
    return {
      nowDate: null,
      nowTime: null,
      isShowBtn: false,
      currentIndex: 0,
      swiperOptions: {
        loop: true,
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev",
        },
        on: {
          slideChange: function(){
            _self.currentIndex = _self.$refs.mySwiper.swiper.realIndex
          }
        },
      },
    };
  },
  components: {
    swiper,
    swiperSlide,
  },
  watch: {
    currentIndex(newVal,oldVal) {
      console.log(newVal,oldVal);
    }
  },
  mounted: {
    
  },
  created() {
    this.nowDate = this.handleDateChange(); //赋值
    setInterval(() => {
      this.nowTime = this.getNowTimes();
    }, 1000);
  },
  methods: {
    jump(type) {
      console.log(type);
      const pathObj = {
        codes: "/codes",
        picture: "/picture",
        text: "/text",
        music: "/music",
      };
      const path = pathObj[type] || "/home";
      this.$router.push({ path });
    },
    handleDateChange() {
      let date = new Date();
      let year = date.getFullYear();
      let mouth = date.getMonth() + 1;
      let dates = date.getDate();
      let arr = [
        "星期日",
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六",
      ];
      let day = date.getDay();
      return `${year}年${mouth}月${dates}日 ${arr[day]}`;
    },
    getNowTimes() {
      let time = new Date();
      let h = time.getHours();
      let m = time.getMinutes();
      let s = time.getSeconds();
      h = h < 10 ? `0${h}` : h;
      m = m < 10 ? `0${m}` : m;
      s = s < 10 ? `0${s}` : s;
      return `${h}:${m}:${s}`;
    },
    // 形参 flag 两个值  true || false
    // isShowBtn 状态只能有两个  true || false
    // 可以直接给isShowBtn = flag
    handleBtnStatus(flag) {
      this.isShowBtn = flag;
    },
  },
  mounted() {},
};
</script>

<style scoped>
.layout {
  border: 1px solid #d7dde4;
  background: #f5f7f9;
  position: relative;
  border-radius: 4px;
  overflow: hidden;
  z-index: 0;
}
.layout-logo {
  width: 260px;
  height: 50px;
  border-radius: 3px;
  float: left;
  position: relative;
  top: 15px;
  left: 20px;
  font-size: 26px;
  color: #fff;
  text-align: center;
  line-height: 50px;
}
.layout-nav {
  width: 500px;
  margin: 0 auto;
  margin-right: 20px;
}
.layout-footer-center {
  text-align: center;
}
.content {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 800px;
  background: url(../../../static/image/img.png) repeat-x 30% 90%;
}
.bread {
  font-size: 23px;
}
.iconText {
  font-size: 20px;
}
.time {
  color: #ffffff;
  font-size: 35px;
  font-weight: bold;
  margin-top: 50px;
  text-align: center;
}

.swiper-slider {
  font-size: 26px;
}
.swiper-button-prev {
  left: 20px;
  width: 30px;
  height: 50px;
  background-size: 30px 50px;
  animation: move 2s 1;
}
.swiper-button-next {
  right: 20px;
  width: 30px;
  height: 50px;
  background-size: 30px 50px;
  animation: move 2s 1;
}
@keyframes move {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.contentBox {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 800px;
  padding: 100px;
  /* background-color: skyblue; */
  background-color: #ffffff;
  background: url(../../../static/image/img.png) 30% 90%;
}
.headImg {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background-color: rebeccapurple;
  background: url(../../../static/image/headImg.jpg);
  background-size: 90% 90%;
}
.signature {
  font-size: 40px;
  color: #f5f7f9;
  line-height: 150px;
}
.info {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 300px;
  height: 300px;
}
</style>




